import { tipoString } from '../predicados';

/**
 * Substitui todos os espaos, tabulaes, quebras de linha e retornos de carro por espaos simples e apara os espaos iniciais e finais.
 * Lana um erro se o parmetro de entrada no  uma string.
 *
 * @param str
 */
export function limparString(str: string): string {
  if (!tipoString(str)) throw new Error('A funo aceita somente strings!');
  return str.replace(/\s+/g, ' ').trim();
}
