import { tipoString } from '../predicados';
import { semAcentuacao } from './semAcentuacao';

/**
 * Remove acentuao e caracteres especiais de uma string.
 * Lana um erro se o parmetro de entrada no  uma string.
 *
 * @param str
 */
export function somentePalavrasAlfanumericas(str: string): string {
  if (!tipoString(str)) throw new Error('A funo aceita somente strings!');
  return semAcentuacao(str).replace(/[^\s\w]/g, '');
}
