import { validarData } from '../validacao';

/**
 * Verifica se uma data  posterior a outra
 *
 * @param data
 * @param ref
 */
export function depoisDe(data: Date, ref: Date): boolean {
  if (!validarData(data) || !validarData(ref)) throw new Error('Data invlida!');
  return data.valueOf() > ref.valueOf();
}
