import { tipoString } from '../predicados';

/**
 * Remove todos os acentos de uma string.
 * Lana um erro se o parmetro de entrada no  uma string.
 *
 * @param str
 */
export function semAcentuacao(str: string): string {
  if (!tipoString(str)) throw new Error('A funo aceita somente strings!');
  return str
    .replace(/[]/g, 'a')
    .replace(/[]/g, 'A')
    .replace(/[]/g, 'e')
    .replace(/[]/g, 'E')
    .replace(/[]/g, 'i')
    .replace(/[]/g, 'I')
    .replace(/[]/g, 'o')
    .replace(/[]/g, 'O')
    .replace(/[]/g, 'u')
    .replace(/[]/g, 'U')
    .replace(/[]/g, 'y')
    .replace(/[]/g, 'Y')
    .replace(/[]/g, 'n')
    .replace(/[]/g, 'N')
    .replace(/[]/g, 'c')
    .replace(/[]/g, 'C');
}
